* {
  margin: 0;
  padding: 0;
  box-sizing: border-box !important;
}

html, body {
  height: 100%;
}


.lines {
  position: absolute;
  top: 0;
  left: 0;
  right: 0;
  height: 100%;
  margin: auto;
  width: 90vw;
}

.line {
  position: absolute;
  width: 1px;
  height: 100%;
  top: 0;
  left: 50%;
  background: transparent;
  overflow: hidden;
  filter: blur(2px);

  &:nth-child(1) {
    margin-left: -50%;

   
  }

  &:nth-child(2) {
    margin-left: 45%;
   
  }

  &:nth-child(3) {
    margin-left: 47%;

  
  }

  &:nth-child(4) {
    margin-left: 49%;

  
  }

  &:nth-child(5) {
    margin-left: -52%;

   
  }
  &:nth-child(6) {
    margin-left: -54%;

   
  }
}

.drop {
  position: absolute;
  height: 15vh;
  width: 100%;
  top: -50%;
  left: 0;
  background: linear-gradient(to bottom, rgba(255, 255, 255, 0) 0%, #9cc0dc 75%, #9cc0dc 100%);
  animation: drop 7s infinite;
  animation-fill-mode: forwards;
  animation-timing-function: cubic-bezier(0.4, 0.26, 0, 0.97);
}

@keyframes drop {
  0% {
    top: -30%;
  }
  100% {
    top: 115%;
  }
}

@media (max-width: 768px) {
  @keyframes drop {
    0% {
      top: -20%;
    }
    20% {
      top: 0%;
    }
    35% {
      top: 20%;
    }
    50% {
      top: 40%;
    }
    65% {
      top: 60%;
    }
    80% {
      top: 80%;
    }
    100% {
      top: 100%;
    }
  }
}